import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import CountryCard from './CountryCard.js';

class App extends Component {

  constructor(props){
	super(props);
	this.state = {
	regions: ["Africa", "Americas", "Asia", "Europe", "Oceania"]
	}
  }
	
componentDidMount() {
	   let apiFetcher = "https://restcountries.eu/rest/v2/alpha/";
	        apiFetcher = apiFetcher.concat(this.props.countryCode)
	        fetch(apiFetcher)
	            .then(r => r.json())
	            .then(data => {
			this.setState({ countryData: data })
		});
	    }	

  render() {
	if (this.state.countryData !== null) {
	return <div>{this.state.countryData.name}</div>
	} else {
	    return <div><p>No country data</p></div>
        }
	  
    return (
      <div className="App">
      	<select onchange="alert()">
	    {this.state.regions.map(region => 
		    (<option value={region}>{region}</option>))}
	</select>
	<CountryCard countryCode="usa"/>
	<CountryCard countryCode="usa"/>

      </div>
    );
  }
}

export default App;
